import React from "react";

import { fireEvent, render, screen } from "@testing-library/react";

import MenuBarButton from "../MenuBarButton";


describe("The MenuBarButton component", () => {
	test("should render properly with text, icon, and avatar.", () => {
		const handleClick = jest.fn(() => { });
		render(
			<MenuBarButton
				dataQA="menu-bar-button"
				buttonText="Button"
				type="button"
				avatarText="MB"
				onClick={handleClick}
			/>,
		);

		const brandButton = screen.getByTestId("menu-bar-button");
		const chevronIcon = screen.getByTestId("ChevronDown-icon");
		const avatarText = screen.getByTestId("menu-bar-button-avatar-text");
		expect(screen.getByText("Button")).toBeInTheDocument();
		expect(brandButton).toBeInTheDocument();
		expect(chevronIcon).toBeInTheDocument();
		expect(avatarText).toBeInTheDocument();
	});

	test("should render properly with image, icon, and no text.", () => {
		const handleClick = jest.fn(() => { });
		render(
			<MenuBarButton
				dataQA="menu-bar-button"
				imageSource="@/ButtonForBrand/__tests__/Brand.png"
				type="button"
				onClick={handleClick}
			/>,
		);

		const brandButtonImage = screen.getByTestId("menu-bar-button-avatar-image");
		expect(brandButtonImage).toBeInTheDocument();
	});

	test("should fire an event when clicked.", () => {
		const handleClick = jest.fn(() => { });
		render(
			<MenuBarButton
				dataQA="menu-bar-button"
				buttonText="Button"
				type="button"
				onClick={handleClick}
			/>,
		);
		fireEvent.click(screen.getByTestId("menu-bar-button"));
		expect(handleClick).toBeCalled();
	});
});
